.veui-console{position:fixed;right:0;bottom:0;left:240px;background-color:#fff}.veui-console h2{height:30px;line-height:30px;margin:0;padding:0 1em;background-color:#f6f9ff;font-size:12px;font-weight:600;color:#666;cursor:pointer}.veui-console h2>*{margin-right:5px}.veui-console h2 .veui-icon{vertical-align:middle}.veui-console h2 small{margin-left:3px}.veui-console .output{overflow:hidden;height:0;margin:0;line-height:20px;font-size:10px;transition:height .2s}.veui-console .output .log{position:relative;min-height:20px;margin:0;padding:0 1em}.veui-console .output .log,.veui-console .output .log .line:not(:last-child){border-bottom:1px solid #f3f3f3}.veui-console .output .log:last-child:before{content:"";position:absolute;top:0;bottom:0;left:0;width:2px;background-color:#369}.veui-console small{color:#999}.veui-console.expanded .output{overflow:auto;height:40vh}


